"use strict";
import { ToggleElement, ToggleWrapper } from "components/Toggle/MultiToggle";
export default function RateToggle({
  currencyA,
  currencyB,
  handleRateToggle
}) {
  const tokenA = currencyA?.wrapped;
  const tokenB = currencyB?.wrapped;
  const isSorted = tokenA && tokenB && tokenA.sortsBefore(tokenB);
  return tokenA && tokenB ? <div style={{ width: "fit-content", display: "flex", alignItems: "center" }} onClick={handleRateToggle}><ToggleWrapper width="fit-content"><ToggleElement isActive={isSorted} fontSize="12px">{isSorted ? currencyA.symbol : currencyB.symbol}</ToggleElement><ToggleElement isActive={!isSorted} fontSize="12px">{isSorted ? currencyB.symbol : currencyA.symbol}</ToggleElement></ToggleWrapper></div> : null;
}
